<template>
  <Nav />
  <div class="wrapper">
    <div>
      <div class="content-header">
        <div>
          <div class="row">
            <div class="col-md-3" style="margin-right: 40px">
              <img
                style="margin-left: 15px; width: 100%; height: auto"
                :src="`${publicPath}sidebar-left.png`"
              />
            </div>
            <div class="col-md-7">
              <div
                style="
                  text-align: center;
                  position: absolute;
                  top: 30%;
                  bottom: 0;
                  left: 0;
                  right: 0;
                  width: 90%;
                "
              >
                <h2>Gugatan Mandiri</h2>
                <br />
                <h3>
                  <a
                    href=""
                    id="okBtn"
                    ref="okBtn"
                    @click.stop.prevent="toggleFab()"
                    >Klik di sini untuk memulai</a
                  >
                </h3>
              </div>
              <!-- chat box -->
              <div
                class="card card-danger direct-chat direct-chat-danger chat"
                style="display:none; top:10%; width: 90%;margin-bottom:100px;"
              >
                <div class="card-header">
                  <div class="chat_option">
                    <div class="header_img">
                      <img :src="`${publicPath}pp-anya.png`" />
                    </div>
                    <span id="chat_head">ANYA</span> <br />
                    <span class="agent">Pengadilan Agama Tulungagung</span>
                    <span class="online">(Online)</span>
                    <span
                      id="chat_fullscreen_loader"
                      class="chat_fullscreen_loader"
                      ><i class="fullscreen zmdi zmdi-window-maximize"></i
                    ></span>
                    <button
                      type="button"
                      style="margin-top: -15px; margin-right: 20px"
                      class="close"
                      data-dismiss="modal"
                      aria-label="Close"
                      @click.stop.prevent="closeChat()"
                    >
                      <span aria-hidden="true">&times;</span>
                    </button>
                  </div>
                </div>
                <div class="chat_body" id="msgs">
                  <!-- chat list -->
                </div>
                <div id="chat_form" class="chat_converse chat_form">
                  <a id="chat_fourth_screen" class="fab"
                    ><i class="zmdi zmdi-arrow-right"></i
                  ></a>
                  <span class="chat_msg_item chat_msg_item_admin">
                    <div class="chat_avatar">
                      <img
                        src="http://res.cloudinary.com/dqvwa7vpe/image/upload/v1496415051/avatar_ma6vug.jpg"
                      />
                    </div>
                    Hey there! Any question?</span
                  >
                  <span class="chat_msg_item chat_msg_item_user"> Hello!</span>
                  <span class="status">20m ago</span>
                  <span class="chat_msg_item chat_msg_item_admin">
                    <div class="chat_avatar">
                      <img
                        src="http://res.cloudinary.com/dqvwa7vpe/image/upload/v1496415051/avatar_ma6vug.jpg"
                      />
                    </div>
                    Agent typically replies in a few hours. Don't miss their
                    reply.
                    <div>
                      <br />
                      <form class="get-notified">
                        <label for="chat_log_email"
                          >Get notified by email</label
                        >
                        <input
                          id="chat_log_email"
                          placeholder="Enter your email"
                        />
                        <i class="zmdi zmdi-chevron-right"></i>
                      </form></div
                  ></span>
                  <span class="chat_msg_item chat_msg_item_admin">
                    <div class="chat_avatar">
                      <img
                        src="http://res.cloudinary.com/dqvwa7vpe/image/upload/v1496415051/avatar_ma6vug.jpg"
                      />
                    </div>
                    Send message to agent.
                    <div>
                      <form class="message_form">
                        <input placeholder="Your email" />
                        <input placeholder="Technical issue" />
                        <textarea
                          rows="4"
                          placeholder="Your message"
                        ></textarea>
                        <button>Send</button>
                      </form>
                    </div></span
                  >
                </div>
                <span
                  style="display: none"
                  class="waiting"
                  title="Petunjuk sedang dibacakan, mohon tunggu terlebih dahulu sampai selesai!"
                ></span>
                <div class="fab_field">
                  <a
                    id="fab_send"
                    @click.stop.prevent="microphoneClick()"
                    class="fab"
                    ><i class="fa fa-microphone icon-to-change"></i
                  ></a>
                  <textarea
                    id="chatSend"
                    name="chat_message"
                    :placeholder="placeholderValue"
                    class="chat_field chat_message"
                    style="margin-top: 10px"
                    disabled
                  ></textarea>
                </div>
              </div>
              <!-- end chat box -->
            </div>
             <div
              class="col-md-1"
              style="margin-bottom:10px;"
            >
              <img style="min-height:85vh;width:120%;max-width: 400px;height: auto;" :src="`${publicPath}anya.png`" />
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import jQuery from "jquery";
const $ = jQuery;
window.$ = $;
import striptags from "striptags";
import Nav from "./partials/Nav.vue";
import { useRouter } from "vue-router";

export default {
  beforeCreate: function () {
    document.body.className = "home-staff";
  },
  components: {
    Nav,
  },
  data() {
    return {
      publicPath: process.env.BASE_URL,
      staffData: "",
      questions: [
        "Sebelum pertanyaan dimulai, kami ingin menginformasikan kepada Anda, jika Anda ingin mengakhiri percakapan ini, ucapkan kata <b>cukup</b>. <br><br> Baik, kita mulai percakapan gugatan mandiri sekarang. <br><br> Di Pengadilan Agama manakah perkara ini akan didaftarkan? Cukup jawab dengan format: <br><b>nama kota</b>",

        // identitas tergugat
        "Siapakah nama Anda?",
        "Sebutkan NIK Anda!",
        "Berapa umur Anda? Cukup sebutkan angkanya!",
        "Apa jenis kelamin Anda? Jawab dengan format: <b>Pria</b> atau <b>Wanita</b>",
        "Apa agama Anda?",
        "Apa pendidikan Anda? SD, SMP, SMA, S1, S2, S3, atau tidak sekolah?",
        "Apa pekerjaan Anda?",
        "Di mana tempat tinggal Anda? Jawab dengan jawaban lengkap disertai RT/RW, Kelurahan, dan Kecamatan!",
        "Sebutkan nomor telepon Anda!",
        "Sebutkan e-mail Anda!",

        // Identitas tergugat (start from index 11)
        "Sebutkan nama pasangan Anda!",
        "Sebutkan umur pasangan Anda! Cukup sebutkan angkanya!",
        "Apa agama pasangan Anda",
        "Apa pendidikan pasangan Anda? SD, SMP, SMA, S1, S2, S3, atau tidak sekolah?",
        "Apa pekerjaan pasangan Anda?",
        "Di mana tempat tinggal pasangan Anda? Jawab dengan jawaban lengkap disertai RT/RW, Kelurahan, dan Kecamatan!",

        // Identitas Pernikahan (start from index 17)
        "Sebutkan tanggal menikah Anda!",
        "Di mana KUA tempat menikah Anda?",
        "Sebutkan nomor kutipan akta nikah Anda!",
        "Sebutkan tanggal kutipan akta nikah Anda!",
        "Setelah menikah, di manakah Anda dan Pasangan Anda hidup bersama dan berapa lama? Jika Anda tinggal bersama pasangan, jawab dengan format:<br> <b>tinggal bersama pasangan di <b>nama tempat tinggal</b> selama <b>lama tinggal</b></b>.<br> Namun jika Anda tidak tinggal bersama pasangan, jawab dengan format: <br><b>tinggal di <b>nama tempat tinggal</b> sedangkan pasangan tinggal di <b>nama tempat tinggal</b> selama <b>lama tinggal</b> </b><br>",
        "Apakah Selama Pernikahan Anda dan pasangan Anda dikaruniai anak? Jika sudah dikaruniai anak, jawab dengan format: <br> <b>sudah dikaruniai anak sebanyak <b>jumlah anak</b></b>.<br> Jika belum dikaruniai anak, jawab hanya dengan format: <br><b>belum dikaruniai anak</b>",

        // start from index 23
        "Sejak kapan rumah tangga Anda tidak harmonis?",
        "Apa penyebab rumah tangga Anda tidak harmonis?",
        "Kapan puncak masalah dari rumah tangga Anda yang tidak harmonis?",
        "Apakah pihak keluarga telah mendamaikan Penggugat dengan Tergugat? Jawab dengan format <b>sudah</b> atau <b>belum</b>.",

        // Start form index 27 khusus untuk penggugat laki-laki
        "Berapa Nafkah Iddah (nafkah Selama masa tunggu ) yang ingin diberikan?",
        "Berapa biaya Mut'ah (tali asih / Kenang-kenangan / Pemberian Mantan Suami kepada Mantan Istri) yang ingin diberikan?",
        // End jika penggugat laki-laki
      ],
      currentQuestion: 0,
      answers: [],
      runtimeTranscription_: "",
      transcription_: [],
      lang_: "id-ID",
      synth: window.speechSynthesis,
      voiceList: [],
      botSpeech: new window.SpeechSynthesisUtterance(),
      placeholderValue: "Send a voice!",
      isClicked: false,
      currentRec: "",
      //userVoice: "",
      indexChatUser: 0,
      indexChatBot: 0,
      countOpenFab: 0,
      voiceTimeout: 0,
      userIsWoman: false,
      router: useRouter(),
      selectedOption: "",
    };
  },
  methods: {
    closeChat() {
      $(".chat").hide();
      this.synth.cancel();
    },
    toggleFab() {
      $(".prime").toggleClass("zmdi-comment-outline");
      $(".prime").toggleClass("zmdi-close");
      $(".prime").toggleClass("is-active");
      $(".prime").toggleClass("is-visible");
      $("#prime").toggleClass("is-float");
      $(".chat").toggleClass("is-visible");
      $(".fab").toggleClass("is-visible");
      this.hideChat(0);

      if ($(".is-visible").is(":hidden")) {
        $(".chat").show();
      } else {
        $(".chat").hide();
      }

      // if user open the chat
      this.countOpenFab = this.countOpenFab + 1;
      if ($(".is-visible").is(":hidden") && this.countOpenFab === 1) {
        // show greeting only at the first time
        let transcriptGreeting = `Selamat datang di Halaman Gugatan Mandiri. Terima kasih telah menghubungi kami. <br>
        <ul>
           <li>Untuk melanjutkan perkara gugatan mandiri, ucapkan <b>gugatan mandiri</b>.</li>
           <li>Untuk mengakses Halaman Utama, ucapkan <b>halaman utama</b>.</li>
           <li>Untuk mengakses halaman syarat-syarat berperkara, ucapkan <b>syarat-syarat berperkara</b>.</li>
        </ul>
        Silahkan tentukan pilihan Anda!`;
        this.greeting(transcriptGreeting);
      } else {
        // if user close the chat, stop window.speechSynthesis(synth.cancel()) and also stop SpeechRecognition()(const recognition)
        this.synth.cancel();
      }
    },
    hideChat(hide) {
      switch (hide) {
        case 0:
          $("#chat_converse").css("display", "none");
          $("#chat_body").css("display", "none");
          $("#chat_form").css("display", "none");
          $(".chat_login").css("display", "block");
          $(".chat_fullscreen_loader").css("display", "none");
          $("#chat_fullscreen").css("display", "none");
          break;
        case 1:
          $("#chat_converse").css("display", "block");
          $("#chat_body").css("display", "none");
          $("#chat_form").css("display", "none");
          $(".chat_login").css("display", "none");
          $(".chat_fullscreen_loader").css("display", "block");
          break;
        case 2:
          $("#chat_converse").css("display", "none");
          $("#chat_body").css("display", "block");
          $("#chat_form").css("display", "none");
          $(".chat_login").css("display", "none");
          $(".chat_fullscreen_loader").css("display", "block");
          break;
        case 3:
          $("#chat_converse").css("display", "none");
          $("#chat_body").css("display", "none");
          $("#chat_form").css("display", "block");
          $(".chat_login").css("display", "none");
          $(".chat_fullscreen_loader").css("display", "block");
          break;
        case 4:
          $("#chat_converse").css("display", "none");
          $("#chat_body").css("display", "none");
          $("#chat_form").css("display", "none");
          $(".chat_login").css("display", "none");
          $(".chat_fullscreen_loader").css("display", "block");
          $("#chat_fullscreen").css("display", "block");
          break;
      }
    },
    microphoneClick() {
      if (this.placeholderValue === "Send a voice!") {
        this.synth.cancel();
        this.isClicked = true;
        this.placeholderValue = "Listening... Please wait!";
        $("#fab_send").css({ "background-color": "#42A5F5" });
        $(".icon-to-change").css({ color: "white" });
        this.recognizeVoice();
      } else {
        if (this.synth.speaking) {
          // if bot still speaking
          // required for record voice automatically (with if else)
          console.log("still speaking");
          $(".waiting").show();
          $(".waiting").tooltip("show");
          setInterval(function () {
            $(".waiting").tooltip("hide").fadeOut(9000).delay(1000);
          }, 1500);
        } else {
          // required for record voice manually (without if else)
          $("#fab_send").css({ "background-color": "white" });
          $(".icon-to-change").css({ color: "#42A5F5" });
          this.placeholderValue = "Send a voice!";
          this.synth.cancel(); // stop current bot speaking.
          this.currentRec.stop();
        }
      }
    },
    voiceTimer() {
      // it's used with longer texts
      if (
        /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(
          navigator.userAgent
        )
      ) {
        this.synth.resume();
        this.voiceTimeout = setTimeout(this.voiceTimer, 10000);
      } else {
        this.synth.pause(); // only work at chrome web, but when we use chrome mobile, we can't use it.
        this.synth.resume();
        this.voiceTimeout = setTimeout(this.voiceTimer, 10000);
      }
    },
    greeting(words) {
      console.log("greeting");
      this.synth.cancel(); // prevent chrome sometimes voice is not found
      this.voiceTimeout = setTimeout(this.voiceTimer, 10000);
      this.voiceList = this.synth.getVoices();
      this.synth.onvoiceschanged = () => {
        this.voiceList = this.synth.getVoices();
      };
      //let transcriptGreeting = this.questions[this.currentQuestion];
      this.botSpeech.text = striptags(words);
      let voices = window.speechSynthesis.getVoices();
      this.botSpeech.voice = voices[11];
      this.botSpeech.lang = "id-ID";
      this.botSpeech.voiceURI = "native";
      this.synth.speak(this.botSpeech);
      if (this.botSpeech.onend) {
        this.botSpeech.onend = function () {
          clearTimeout(this.voiceTimeout);
        };
      }

      // Show greeting in a chatbox
      this.indexChatBot = this.indexChatBot + 1;
      $(".chat_body").append(
        `<div class="chat-default" style="width: 80%; margin-left: 5px; text-align: left;"></div>`
      ); // add new element inside chat_body
      $(`.chat-default`).append(
        `<img class="direct-chat-img" src='${this.publicPath}pp-anya.png'>`
      );
      $(`.chat-default`).append(
        `<div class="direct-chat-text greeting">${words}</div>`
      );
    },
    recognizeVoice() {
      window.SpeechRecognition =
        window.SpeechRecognition || window.webkitSpeechRecognition;
      const recognition = new window.SpeechRecognition();
      recognition.lang = this.lang_;
      recognition.interimResults = true;

      // event current voice reco word
      recognition.addEventListener("result", (event) => {
        var text = Array.from(event.results)
          .map((result) => result[0])
          .map((result) => result.transcript)
          .join("");
        this.runtimeTranscription_ = text;
      });

      this.currentRec = recognition;

      this.startSpeechToTxt();
    },
    startSpeechToTxt() {
      console.log("listening...");
      var self = this;

      // end of transcription
      this.currentRec.addEventListener("end", () => {
        this.transcription_ = [];
        this.transcription_.push(this.runtimeTranscription_);

        if (this.transcription_[0] !== "") {
          if (this.transcription_[0] === "halaman utama") {
            let transcript2 = `Mohon tunggu sebentar, kami akan mengantarkan Anda ke Halaman Utama.`;
            this.showBotVoiceQuestion(transcript2);
            this.showUserVoiceAsText(this.transcription_[0]);
            this.showBotVoiceAsText(transcript2);
          } else if (this.transcription_[0] === "syarat-syarat berperkara") {
            let transcript1 = `Mohon tunggu sebentar, kami akan mengantarkan Anda ke halaman syarat-syarat berperkara.`;
            this.showBotVoiceQuestion(transcript1);
            this.showUserVoiceAsText(this.transcription_[0]);
            this.showBotVoiceAsText(transcript1);
          } else if (this.transcription_[0] === "gugatan Mandiri") {
            this.selectedOption = "gugatan Mandiri";
            let startQuestion = this.questions[this.currentQuestion];
            this.showBotVoiceQuestion(startQuestion);
            this.showUserVoiceAsText(this.transcription_[0]);
            this.showBotVoiceAsText(startQuestion);
          }

          ////////////////////////////////////////////////////////////////////////////////////////
          else {
            if (this.selectedOption === "gugatan Mandiri") {
              this.currentQuestion = this.currentQuestion + 1;
              this.showUserVoiceAsText(this.transcription_[0]);

              if (
                this.currentQuestion === 5 &&
                this.transcription_[0] === "wanita"
              ) {
                this.questions.length = 26; // Skip all the questions start from index 27 to 28
                this.userIsWoman = true;
              }

              // If the questions is end
              if (this.questions[this.currentQuestion] === undefined) {
                let transcript =
                  "Terima kasih atas waktunya! Untuk mencetak data Anda, silahkan pergi ke halaman Narasi!";
                this.showBotVoiceQuestion(transcript);
                this.showBotVoiceAsText(transcript);
                this.saveDataToDB();
              } else {
                if (this.transcription_[0] === "cukup") {
                  let transcriptCukup = `Terima kasih atas waktunya! Untuk mencetak data Anda, silahkan pergi ke halaman Narasi!`;
                  this.showBotVoiceQuestion(transcriptCukup);
                  this.showBotVoiceAsText(transcriptCukup);
                  this.saveDataToDB();
                } else {
                  // continue questions
                  this.answers.push(this.transcription_[0]);
                  this.showBotVoiceQuestion(
                    this.questions[this.currentQuestion]
                  );
                  this.showBotVoiceAsText(this.questions[this.currentQuestion]);
                }
              }
            } else {
              this.synth.cancel();
              this.botSpeech.text =
                "Maaf, pilihan Anda tidak tersedia, silahkan pilih opsi lain!";
              this.synth.speak(this.botSpeech);

              this.botSpeech.onend = function () {
                self.recognizeVoice();
              };
            }
          }
        } else if (
          this.transcription_[0] === "" &&
          this.placeholderValue === "Listening... Please wait!"
        ) {
          this.synth.cancel();
          this.botSpeech.text =
            "Maaf, kami tidak mendengar suara Anda. Silahkan coba lagi!";
          this.synth.speak(this.botSpeech);

          // required for record voice manually
          // this.currentRec.stop();
          // this.botSpeech.onend = function () {
          //   self.synth.cancel();
          // };
          // this.placeholderValue = "Send a voice!";
          // $("#fab_send").css({ "background-color": "white" });
          // $(".icon-to-change").css({ color: "#42A5F5" });

          // required for record voice automatically
          this.botSpeech.onend = function () {
            self.recognizeVoice();
          };
        }

        this.runtimeTranscription_ = "";
        this.currentRec.stop();
      });
      this.currentRec.start();
    },
    showBotVoiceQuestion(transcript) {
      this.synth.cancel(); // stop current bot speaking (prevent chrome sometimes voice is not found)
      this.voiceTimeout = setTimeout(this.voiceTimer, 100000);
      this.botSpeech.text = striptags(transcript);
      this.synth.speak(this.botSpeech);
      //this.currentRec.stop(); // required for record voice manually
      if (this.botSpeech.onend) {
        this.botSpeech.onend = function () {
          clearTimeout(this.voiceTimeout);
        };
      }

      // required for record voice automatically
      let lastVoice = this.voiceList[this.voiceList.length - 1];
      if (lastVoice) {
        var self = this;
        this.botSpeech.onend = function () {
          //self.recognizeVoice();
          self.goToTheCertainPage();
        };
      }

      // required for record voice manually
      // this.placeholderValue = "Send a voice!";
      // $("#fab_send").css({ "background-color": "white" });
      // $(".icon-to-change").css({ color: "#42A5F5" });
    },
    showUserVoiceAsText(transcript) {
      this.indexChatUser = this.indexChatUser + 1;
      $(".chat_body").append(
        `<div class='direct-chat-msg right user-chat-${
          this.indexChatUser + 1
        }' style="width: 98%; text-align: left;"></div>`
      );
      $(`.user-chat-${this.indexChatUser + 1}`).append(
        "<img class='direct-chat-img' src='data:image/jpeg;base64,'/>"
      );
      $(`.user-chat-${this.indexChatUser + 1}`).append(
        `<div class="direct-chat-text user-text" style="margin-left:55px;">${transcript}</div>`
      );
    },
    showBotVoiceAsText(transcript) {
      this.indexChatBot = this.indexChatBot + 1;
      $(".chat_body").append(
        `<div class='direct-chat-msg chat-default-${
          this.indexChatBot + 1
        }' style="width: 80%; margin-left: 5px; text-align: left;"></div>`
      ); // add new element (direct-chat-msg) inside chat_body
      $(`.chat-default-${this.indexChatBot + 1}`).append(
        `<img class="direct-chat-img" src='${this.publicPath}pp-anya.png'>`
      );
      $(`.chat-default-${this.indexChatBot + 1}`).append(
        `<div class="direct-chat-text bot-voice">${transcript}</div>`
      );

      var div = $(".chat_body");
      div.scrollTop(div.prop("scrollHeight"));
    },
    goToTheCertainPage() {
      if (this.transcription_[0] === "halaman utama") {
        return this.router.push({
          name: "welcome",
        });
      } else if (this.transcription_[0] === "syarat-syarat berperkara") {
        return this.router.push({
          name: "syarat-berperkara",
        });
      } else {
        this.recognizeVoice();
      }
    },
    saveDataToDB() {
      let date = new Date().toLocaleDateString("en-us", { day: "numeric" });
      let month = new Date().toLocaleDateString("id-ID", { month: "long" });
      let year = new Date().toLocaleDateString("en-us", { year: "numeric" });
      const FormattedDate = `${date}-${month}-${year}`;

      // for pdf file: margin-left: 3cm;margin-right: 2cm; margin-top:2cm; margin-bottom:3cm; 
      let opening = `
      <body style="margin-left: 1.5cm;margin-right: 2cm; margin-bottom:3cm; font-family: Arial, Helvetica, sans-serif; font-size: 12px;">
      <p style="text-indent: 0pt;text-align: right;">${this.answers[0] === undefined ? '_____' : this.answers[0]}, ${FormattedDate}</p> 
      <br>
      <br>
      Kepada Yth. <br>
      Ketua Pengadilan Agama ${this.answers[0] === undefined ? '_____' : this.answers[0]} <br>
      Di ${this.answers[0] === undefined ? '_____' : this.answers[0]}. <br>
      <br>
      Assalamu'alaikum Wr. Wb.<br>
      <br>
      `;

      let penggugat = `
      Yang bertanda tangan di bawah ini: <br>
      Nama : ${this.answers[1] === undefined ? '_____' : this.answers[1]} <br>
      NIK : ${this.answers[2] === undefined ? '_____' : this.answers[2]} <br>
      Umur : ${this.answers[3] === undefined ? '_____' : this.answers[3]} Tahun <br>
      Agama : ${this.answers[5] === undefined ? '_____' : this.answers[5]} <br>
      Pendidikan :${this.answers[6] === undefined ? '_____' : this.answers[6]} <br>
      Pekerjaan : ${this.answers[7] === undefined ? '_____' : this.answers[7]} <br>
      Tempat tinggal : ${this.answers[8] === undefined ? '_____' : this.answers[8]} <br>
